//mixin으로 중복되는 스타일링 설정하기 
//include로 불러옴

@mixin 함수() {
    background: #eeeeee;
    padding: 20px;
    border-radius: 5px;
    text-align: center;
    max-width: 500px;
    width: 100%;
    margin: auto;   
}

//자주쓰는 속성이나 css 변수로 설정
$메인컬러 : #ff0000;


.wow-enter {
    opacity: 0;
}
.wow-enter-active {
    opacity: 1;
    transition: all 500ms;
}


.red{
    color:$메인컬러;
}

.my-alert{
    //mixin만들어뒀던거 불러오기
    @include 함수()
    p{
        margin: 0;
    }
}

.my-alert2 {
    //my-alert의 모든 속성을 가져온다.
    @extend .my-alert;
    //추가설정이나 수정하고 싶으면 그대로 쓰면됨!
}






